// src/utils/aiPromptEngine.js
import { formatCurrency, formatPercentage, formatDays } from './formatters';
import { PERIOD_TYPES, AI_PROVIDERS } from './constants';
import { ANALYSIS_TYPES, ANALYSIS_METADATA } from './aiAnalysisTypes';
import { fieldDefinitions, getFieldKeys } from './fieldDefinitions';

// --- Helper: Build a detailed summary for AI context ---
function buildFullFinancialDataSummary(calculatedData, periodTypeLabel, companyName, reportTitle) {
  if (!calculatedData || calculatedData.length === 0) return "Nenhum dado financeiro disponvel para resumir.";

  let summary = `ANLISE FINANCEIRA PARA: ${companyName} - ${reportTitle}\n`;
  summary += `TIPO DE PERODO: ${PERIOD_TYPES[periodTypeLabel]?.label || periodTypeLabel}\n`;
  summary += `NMERO DE PERODOS ANALISADOS: ${calculatedData.length}\n\n`;

  summary += "DADOS FINANCEIROS CHAVE POR PERODO (Valores em BRL, Prazos em Dias, Percentuais em %):\n";
  calculatedData.forEach((p, i) => {
    summary += `\n--- PERODO ${i + 1} (${PERIOD_TYPES[periodTypeLabel]?.shortLabel || 'Per.'} ${i + 1}) ---\n`;
    // P&L Highlights
    summary += `Receita: ${formatCurrency(p.revenue)}\n`;
    summary += `Custo dos Produtos/Servios Vendidos (CPV/CSV): ${formatCurrency(p.cogs)}\n`;
    summary += `Lucro Bruto: ${formatCurrency(p.grossProfit)} (Margem Bruta: ${formatPercentage(p.gmPct)})\n`;
    summary += `Despesas Operacionais: ${formatCurrency(p.operatingExpenses)}\n`;
    summary += `EBITDA: ${formatCurrency(p.ebitda)}\n`;
    summary += `Depreciao e Amortizao: ${formatCurrency(p.depreciationAndAmortisation)}\n`;
    summary += `Lucro Operacional (EBIT): ${formatCurrency(p.ebit)} (Margem EBIT: ${formatPercentage(p.opProfitPct)})\n`;
    summary += `Resultado Financeiro Lquido: ${formatCurrency(p.netInterestExpenseIncome)}\n`;
    summary += `Itens Extraordinrios: ${formatCurrency(p.extraordinaryItems)}\n`;
    summary += `Lucro Antes do IR (PBT): ${formatCurrency(p.pbt)}\n`;
    summary += `Imposto de Renda: ${formatCurrency(p.incomeTax)}\n`;
    summary += `Lucro Lquido: ${formatCurrency(p.netProfit)} (Margem Lquida: ${formatPercentage(p.netProfitPct)})\n`;
    summary += `Dividendos Pagos: ${formatCurrency(p.dividendsPaid)}\n`;
    summary += `Lucro Retido: ${formatCurrency(p.retainedProfit)}\n\n`;

    // Working Capital & Balance Sheet Highlights
    summary += `Caixa Inicial: ${formatCurrency(p.openingCash)}\n`;
    summary += `Contas a Receber (Valor Mdio): ${formatCurrency(p.accountsReceivableValueAvg)} (PMR Derivado: ${formatDays(p.arDaysDerived)})\n`;
    summary += `Estoques (Valor Mdio): ${formatCurrency(p.inventoryValueAvg)} (PME Derivado: ${formatDays(p.inventoryDaysDerived)})\n`;
    summary += `Contas a Pagar (Valor Mdio): ${formatCurrency(p.accountsPayableValueAvg)} (PMP Derivado: ${formatDays(p.apDaysDerived)})\n`;
    summary += `Capital de Giro (Operacional): ${formatCurrency(p.workingCapitalValue)} (Ciclo de Caixa: ${formatDays(p.wcDays)})\n`;
    summary += `Variao do Capital de Giro (Investimento): ${formatCurrency(p.workingCapitalChange)}\n`;
    summary += `Ativo Imobilizado Lquido (Saldo Final): ${formatCurrency(p.netFixedAssets)}\n`;
    summary += `Total Emprstimos Bancrios (Saldo Final): ${formatCurrency(p.totalBankLoans)}\n`;
    summary += `Patrimnio Lquido (Saldo Final): ${formatCurrency(p.equity)}\n`;
    summary += `Total Ativos Estimado: ${formatCurrency(p.estimatedTotalAssets)}\n`;
    summary += `Total Passivos Estimado: ${formatCurrency(p.estimatedTotalLiabilities)}\n`;
    summary += `DIFERENA DE BALANO (Ativo - (Passivo+PL)): ${formatCurrency(p.balanceSheetDifference)}\n\n`;

    // Cash Flow Highlights
    summary += `Fluxo de Caixa Operacional (FCO): ${formatCurrency(p.operatingCashFlow)}\n`;
    summary += `CAPEX (Invest. Ativo Imob.): ${formatCurrency(p.capitalExpenditures)}\n`;
    summary += `Caixa Gerado Operaes (aps CG): ${formatCurrency(p.cashFromOpsAfterWC)}\n`;
    summary += `Fluxo de Caixa Livre (Antes do Financiamento): ${formatCurrency(p.netCashFlowBeforeFinancing)}\n`;
    summary += `Variao em Emprstimos: ${formatCurrency(p.changeInDebt)}\n`;
    summary += `Fluxo de Caixa de Financiamento: ${formatCurrency(p.cashFlowFromFinancing)}\n`;
    summary += `Variao Lquida de Caixa: ${formatCurrency(p.netChangeInCash)}\n`;
    summary += `Caixa Final (Calculado): ${formatCurrency(p.closingCash)}\n`;
    summary += `Necessidade (-) / Excedente (+) de Financiamento (FCO - CG - CAPEX): ${formatCurrency(p.fundingGapOrSurplus)}\n`;
  });
  summary += "\n--- FIM DOS DADOS FINANCEIROS ---\n";
  return summary;
}

function createRiskAssessmentPrompt(financialDataBundle, providerKey, options) {
  const { calculatedData, companyInfo } = financialDataBundle;
  const financialSummary = buildFullFinancialDataSummary(calculatedData, companyInfo.periodType, companyInfo.name, companyInfo.reportTitle);

  return `VOC  UM ESPECIALISTA EM GESTO DE RISCOS FINANCEIROS com experincia em anlise de solvncia, liquidez e sustentabilidade empresarial.

EMPRESA: ${companyInfo.name}
ANLISE: Avaliao de Riscos para ${companyInfo.reportTitle}
PERODOS ANALISADOS: ${calculatedData.length} (${PERIOD_TYPES[companyInfo.periodType]?.label || companyInfo.periodType})

DADOS FINANCEIROS DETALHADOS:
${financialSummary}

FRAMEWORK DE AVALIAO DE RISCOS (FOQUE NO LTIMO PERODO E NAS TENDNCIAS):

1.   RISCOS DE LIQUIDEZ:

      - Adequao do Saldo Final de Caixa vs. Despesas Operacionais mdias ou CPV.
      - Capacidade de honrar compromissos de curto prazo (analise Passivo Circulante Estimado vs. Ativo Circulante Estimado, se possvel com os dados).
      - Qualidade e tendncia do Fluxo de Caixa Operacional (FCO).
      - Ciclo de Converso de Caixa: nvel e tendncia.

2.   RISCOS OPERACIONAIS E DE RENTABILIDADE:

      - Sustentabilidade e tendncia das Margens (Bruta, EBIT, Lquida).
      - Concentrao de receitas (no infervel dos dados, mas mencione como ponto de ateno se outros indicadores so fracos).
      - Evoluo das Despesas Operacionais em relao  Receita.

3.   RISCOS FINANCEIROS (ENDIVIDAMENTO E ESTRUTURA DE CAPITAL):

      - Nvel de Emprstimos Bancrios Totais em relao ao Patrimnio Lquido.
      - Capacidade de cobertura de juros (EBIT / Despesa Financeira Lquida  se Desp. Fin. for negativa/despesa).
      - Tendncia da Necessidade/Excedente de Financiamento (fundingGapOrSurplus).

4.   RISCOS DE CAPITAL DE GIRO:

      - Evoluo e nveis dos prazos (PMR Derivado, PME Derivado, PMP Derivado).
      - Impacto da Variao do Capital de Giro no caixa.

5.   RISCOS DE QUALIDADE DA INFORMAO:

      - Avalie a "Diferena de Balano". Se for significativa (ex: > 2-5% do Total de Ativos Estimado), classifique como um risco  confiabilidade da anlise e recomende reviso dos inputs.

FORMATO DE RESPOSTA (MARKDOWN, EM PORTUGUS DO BRASIL):

##  AVALIAO DE RISCOS FINANCEIROS

### Principais Riscos Identificados (Priorizados)

(Liste de 3 a 5 riscos mais importantes, combinando severidade e probabilidade inferida dos dados)

1.  ** [NOME CONCISO DO RISCO 1 - Ex: Risco de Liquidez Elevado]**

      * **Descrio:** [Explicao detalhada do risco, suportada por mtricas e tendncias dos dados fornecidos. Ex: "O Caixa Final de R$X representa apenas Y dias de Despesas Operacionais, e o FCO tem sido consistentemente negativo..."]
      * **Mtricas Chave Associadas:** [PMR, FCO/Receita, Diferena de Balano, etc.]
      * **Impacto Potencial:** [Consequncias se o risco se materializar]

2.  ** [NOME CONCISO DO RISCO 2 - Ex: Risco de Rentabilidade Declinante]**

      * **Descrio:** [...]
      * **Mtricas Chave Associadas:** [...]
      * **Impacto Potencial:** [...]

3.  **[Repetir para outros riscos significativos]**

###  Pontos de Menor Risco Relativo ou Fortalezas Observadas

  - **[Aspecto Positivo 1]:** [Descrio e dados que suportam]
  - **[Aspecto Positivo 2]:** [Descrio e dados que suportam]

##  INDICADORES DE ALERTA (RED FLAGS)

(Liste 2-3 indicadores que esto em nveis preocupantes ou com tendncias negativas fortes)

1.  **[Indicador Especfico]:** Valor atual [X] no ltimo Perodo. [Breve comentrio sobre por que  um alerta].
2.  **[Indicador Especfico]:** ...

##  RECOMENDAES DE MITIGAO (TOP 2-3 GERAIS)

(Sugestes gerais para os riscos mais crticos identificados)

1.  **Para o Risco de [Nome do Risco 1]:** [Ao de mitigao sugerida, ex: "Revisar e otimizar o ciclo de contas a receber para reduzir o PMR." ou "Analisar a estrutura de custos operacionais para identificar oportunidades de reduo."]
2.  **Para o Risco de [Nome do Risco 2]:** [...]

-----

Baseie toda anlise ESTRITAMENTE nos dados financeiros fornecidos. Quantifique sempre que possvel.`;
}

function createCashFlowAnalysisPrompt(financialDataBundle, providerKey, options) {
  const { calculatedData, companyInfo } = financialDataBundle;
  const financialSummary = buildFullFinancialDataSummary(calculatedData, companyInfo.periodType, companyInfo.name, companyInfo.reportTitle);

  return `VOC  UM ESPECIALISTA EM FLUXO DE CAIXA E GESTO DE TESOURARIA, com foco em diagnstico e otimizao.

EMPRESA: ${companyInfo.name}
ANLISE: Anlise Detalhada do Fluxo de Caixa para ${companyInfo.reportTitle}
PERODOS ANALISADOS: ${calculatedData.length} (${PERIOD_TYPES[companyInfo.periodType]?.label || companyInfo.periodType})

DADOS FINANCEIROS COMPLETOS (INCLUINDO P&L, BALANO ESTIMADO E FLUXO DE CAIXA DETALHADO):
${financialSummary}

INSTRUES PARA ANLISE DETALHADA DO FLUXO DE CAIXA:

1.  **FLUXO DE CAIXA OPERACIONAL (FCO):**

      * Analise a magnitude e tendncia do FCO.  positivo e suficiente?
      * Compare o FCO com o Lucro Lquido. O que explica as diferenas (principalmente D&A e Variao do Capital de Giro)?
      * Avalie a qualidade e sustentabilidade do FCO.

2.  **INVESTIMENTO EM CAPITAL DE GIRO (Working Capital Change):**

      * Interprete o valor da "Variao do Capital de Giro". Um valor positivo representa um aumento no investimento em capital de giro (uso de caixa), e um negativo, uma liberao de caixa.
      * Analise a tendncia. O capital de giro est consumindo caixa consistentemente? Isso  sustentvel?

3.  **INVESTIMENTOS (CAPEX - \`capitalExpenditures\`):**

      * Qual o nvel de CAPEX? Est alinhado com a Depreciao (como proxy de manuteno) ou sugere expanso/contrao?
      * Impacto do CAPEX no caixa.

4.  **FLUXO DE CAIXA LIVRE (FCL - \`netCashFlowBeforeFinancing\`):**

      * Este  o caixa gerado aps todas as atividades operacionais e de investimento. Qual sua magnitude e tendncia?
      * A empresa gera caixa livre positivo de forma consistente?

5.  **FLUXO DE CAIXA DE FINANCIAMENTO (FCF):**

      * Analise os componentes: \`changeInDebt\` (Variao em Emprstimos) e \`dividendsPaid\`.
      * A empresa est tomando mais dvida, pagando dvida, ou pagando dividendos significativos?
      * Como o FCF se relaciona com o FCL Antes do Financiamento?

6.  **POSIO DE CAIXA (\`closingCash\`):**

      * Analise a evoluo do Saldo Final de Caixa. Est aumentando, diminuindo, estvel?
      * O nvel de caixa  adequado para as operaes da empresa? (Pode ser qualitativo se no houver dados de despesas mdias dirias).

7.  **NECESSIDADE/EXCEDENTE DE FINANCIAMENTO (\`fundingGapOrSurplus\`):**

      * Este valor  \`-(FCO - Variao CG - CAPEX)\`. Um valor positivo aqui significa que as operaes e investimentos (antes do financiamento externo de dvida/equity) geraram um excedente de caixa. Um valor negativo significa uma necessidade de caixa que teve que ser coberta por financiamento ou reduo de caixa.
      * Interprete este valor para o ltimo perodo e sua tendncia.

FORMATO DE RESPOSTA (MARKDOWN, EM PORTUGUS DO BRASIL):

##  ANLISE DETALHADA DO FLUXO DE CAIXA

### 1. Gerao de Caixa Operacional (FCO)

  - **Desempenho e Tendncia:** [Avaliao: Forte/Fraco, Crescente/Decrescente. Ex: "FCO positivo de R$X no ltimo perodo, mostrando uma melhora de Y% em relao ao perodo anterior..."]
  - **Qualidade:** [Comparao com Lucro Lquido. Ex: "O FCO  Z% superior ao Lucro Lquido devido principalmente  D&A, indicando boa converso de lucro em caixa..."]

### 2. Investimento em Capital de Giro

  - **Impacto no Caixa:** [Anlise da \`workingCapitalChange\`. Ex: "Houve um investimento (uso de caixa) de R$X em Capital de Giro no ltimo perodo, principalmente devido ao aumento do PMR..."]
  - **Tendncia e Sustentabilidade:** [Avaliao]

### 3. Investimentos (CAPEX)

  - **Nvel e Tendncia:** [Avaliao do \`capitalExpenditures\`. Ex: "CAPEX de R$X, representando Y% da Receita, sugere manuteno/expanso..."]

### 4. Fluxo de Caixa Livre (Antes do Financiamento)

  - **Capacidade de Gerao:** [Anlise do \`netCashFlowBeforeFinancing\`. Ex: "FCL antes do Financiamento foi de R$X, indicando que a empresa gerou/consumiu caixa aps operaes e investimentos..."]
  - **Tendncia:** [Avaliao]

### 5. Atividades de Financiamento

  - **Fontes e Usos:** [Anlise de \`changeInDebt\` e \`dividendsPaid\`. Ex: "A empresa tomou R$X em novos emprstimos e pagou R$Y em dividendos..."]

### 6. Posio Final de Caixa

  - **Evoluo e Nvel:** [Anlise do \`closingCash\`. Ex: "O saldo de caixa aumentou para R$X, um nvel que parece adequado/preocupante..."]

### 7. Necessidade ou Excedente de Financiamento Estrutural

  - **Interpretao do \`fundingGapOrSurplus\`:** [Ex: "A empresa apresentou uma necessidade de financiamento estrutural de R$X no ltimo perodo, indicando que as operaes e investimentos consumiram mais caixa do que geraram, antes de considerar novas dvidas ou aportes de capital..."]

###  Concluses e Recomendaes Chave para Fluxo de Caixa (Top 2-3)

1.  **[Ponto Crtico/Oportunidade 1]:** [Ex: "Melhorar a gesto do ciclo de caixa, focando na reduo do PMR..."]
    **Ao Sugerida:** [Ex: "Implementar polticas de crdito mais rigorosas e otimizar processos de cobrana." ]
2.  **[Ponto Crtico/Oportunidade 2]:** [...]
    **Ao Sugerida:** [...]

-----

Utilize os dados financeiros fornecidos para quantificar todas as suas anlises e concluses.`;
}

function createStrategicRecommendationsPrompt(financialDataBundle, providerKey, options) {
  const { calculatedData, companyInfo } = financialDataBundle;
  const financialSummary = buildFullFinancialDataSummary(calculatedData, companyInfo.periodType, companyInfo.name, companyInfo.reportTitle);
  return `VOC  UM CONSULTOR DE ESTRATGIA EMPRESARIAL SNIOR.\n\nEMPRESA: ${companyInfo.name}\nANLISE SOLICITADA: Recomendaes Estratgicas para ${companyInfo.reportTitle}\nPERODOS ANALISADOS: ${calculatedData.length} (${PERIOD_TYPES[companyInfo.periodType]?.label || companyInfo.periodType})\n\nDADOS FINANCEIROS COMPLETOS:\n${financialSummary}\n\nMISSO: Fornea de 3 a 5 RECOMENDAES ESTRATGICAS ACIONVEIS e de ALTO IMPACTO.\nFOCO: Rentabilidade, Eficincia, Liquidez, Crescimento, Estrutura de Capital, Mitigao de Riscos.\n\nFORMATO DE RESPOSTA (MARKDOWN, EM PORTUGUS DO BRASIL):\n##  RECOMENDAES ESTRATGICAS DE ALTO IMPACTO\n### Recomendao 1: [Ttulo]\n- **Diagnstico Financeiro Base:** [Problema ou oportunidade suportado por dados]\n- **Ao Estratgica Proposta:** [Descrio clara e detalhada]\n- **Impacto Esperado:** [Quantificao do benefcio]\n- **Principais Passos para Implementao:** [Passos resumidos]\n- **Mtricas Chave de Sucesso:** [KPIs]\n### Recomendao 2: ...\n...\n\nJustifique cada recomendao com base nos dados fornecidos.`;
}

